.header {
  height: 80px;
  display: flex;
  align-items: center;
  background: var(--background-white);
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0 24px;
  box-shadow: var(--shadow-sm);
  border-bottom: 1px solid var(--border-light);
}

.header__logo {
  width: 120px;
  height: 40px;
  object-fit: contain;
  margin-right: 32px;
  cursor: pointer;
  filter: brightness(0) saturate(100%) invert(20%) sepia(77%) saturate(1204%) hue-rotate(209deg) brightness(95%) contrast(86%);
}

.header__location {
  display: flex;
  align-items: center;
  color: var(--text-secondary);
  padding: 8px 12px;
  margin-right: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header__location:hover {
  background-color: var(--background-light);
  color: var(--text-primary);
}

.header__locationIcon {
  margin-right: 6px;
  color: var(--accent-teal);
}

.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 12px;
  overflow: hidden;
  max-width: 600px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-light);
  background: var(--background-white);
}

.header__searchCategory {
  background-color: var(--background-light);
  color: var(--text-secondary);
  padding: 12px 16px;
  font-size: 14px;
  font-weight: 500;
  border-right: 1px solid var(--border-light);
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header__searchCategory:hover {
  background-color: var(--border-light);
  color: var(--text-primary);
}

.header__searchInput {
  height: 48px;
  padding: 0 16px;
  border: none;
  width: 100%;
  font-size: 16px;
  outline: none;
  background: transparent;
  color: var(--text-primary);
}

.header__searchInput::placeholder {
  color: var(--text-muted);
}

.header__searchIconContainer {
  padding: 12px 16px;
  height: 48px;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header__searchIconContainer:hover {
  opacity: 0.9;
  transform: scale(1.02);
}

.header__searchIcon {
  color: white;
}

.header__basketIconContainer {
  position: relative;
  display: flex;
}

.header__optionBasket {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--text-primary);
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header__optionBasket:hover {
  background-color: var(--background-light);
}

.header__basketCount {
  position: absolute;
  top: -2px;
  right: -2px;
  background: var(--gradient-secondary);
  color: white;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
  box-shadow: var(--shadow-sm);
}

.header__nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 8px;
}

.header__option {
  display: flex;
  flex-direction: column;
  color: var(--text-primary);
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.header__option:hover {
  background-color: var(--background-light);
}

.header__optionLineOne {
  font-size: 12px;
  color: var(--text-muted);
  font-weight: 400;
}

.header__optionLineTwo {
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  color: var(--text-primary);
}

a {
  color: inherit;
  text-decoration: none;
}

/* Responsive design */
@media (max-width: 768px) {
  .header {
    flex-wrap: wrap;
    height: auto;
    padding: 10px;
  }
  
  .header__logo {
    margin: 0 auto;
    margin-bottom: 10px;
  }
  
  .header__location {
    display: none;
  }
  
  .header__search {
    order: 3;
    width: 100%;
    margin: 10px 0;
  }
  
  .header__nav {
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
  }
  
  .header__option {
    margin: 5px;
    min-width: auto;
  }
}.header__
optionWishlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--text-primary);
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header__optionWishlist:hover {
  background-color: var(--background-light);
  color: var(--error);
}