.login {
  background: var(--background-light);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 24px;
}

.login__logo {
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  color: var(--primary-dark);
  cursor: pointer;
}

.login__container {
  width: 100%;
  max-width: 400px;
  background: var(--background-white);
  display: flex;
  flex-direction: column;
  border-radius: 16px;
  border: 1px solid var(--border-light);
  padding: 40px 32px;
  margin-bottom: 32px;
  box-shadow: var(--shadow-lg);
}

.login__container > h1 {
  font-weight: 700;
  font-size: 32px;
  margin-bottom: 32px;
  line-height: 1.2;
  color: var(--text-primary);
  text-align: center;
}

.login__container > form > h5 {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.login__container > form > input {
  height: 48px;
  margin-bottom: 20px;
  background-color: var(--background-white);
  width: 100%;
  padding: 12px 16px;
  border: 2px solid var(--border-light);
  border-radius: 8px;
  outline: none;
  font-size: 16px;
  transition: all 0.2s ease;
  color: var(--text-primary);
}

.login__container > form > input:focus {
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(49, 130, 206, 0.1);
}

.login__container > form > input.error {
  border-color: var(--error);
}

.login__container > p {
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.5;
  color: var(--text-secondary);
  text-align: center;
}

.login__signInButton {
  background: var(--gradient-primary);
  border-radius: 8px;
  width: 100%;
  height: 48px;
  border: none;
  margin-top: 20px;
  cursor: pointer;
  color: white;
  font-size: 16px;
  font-weight: 600;
  box-shadow: var(--shadow-sm);
  transition: all 0.2s ease;
}

.login__signInButton:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.login__signInButton:active {
  transform: translateY(0);
}

.login__divider {
  position: relative;
  text-align: center;
  margin: 32px 0;
  height: 1px;
  background-color: var(--border-light);
}

.login__dividerText {
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--background-white);
  padding: 0 16px;
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
}

.login__registerButton {
  background: var(--background-white);
  border-radius: 8px;
  width: 100%;
  height: 48px;
  border: 2px solid var(--border-medium);
  margin-top: 16px;
  cursor: pointer;
  color: var(--text-primary);
  font-size: 16px;
  font-weight: 600;
  transition: all 0.2s ease;
}

.login__registerButton:hover {
  background: var(--background-light);
  border-color: var(--accent-blue);
  transform: translateY(-1px);
}

.login__footer {
  width: 100%;
  max-width: 400px;
  margin-top: 32px;
  text-align: center;
}

.login__footerLinks {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-bottom: 16px;
  flex-wrap: wrap;
}

.login__footerLinks a {
  color: var(--accent-blue);
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.login__footerLinks a:hover {
  color: var(--accent-purple);
}

.login__footerCopyright {
  font-size: 12px;
  color: var(--text-muted);
}

/* Responsive design */
@media (max-width: 768px) {
  .login__container {
    width: 85%;
    max-width: 350px;
  }
  
  .login__footer {
    width: 85%;
    max-width: 350px;
  }
}