.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1400px;
  background-color: var(--background-light);
  padding: 0 24px;
}

.home__container {
  width: 100%;
}

.home__carousel {
  width: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 16px;
  margin-bottom: 40px;
  box-shadow: var(--shadow-lg);
}

.home__image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 16px;
}

.home__row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

/* Featured products section */
.home__featured {
  background: var(--background-white);
  margin-bottom: 48px;
  padding: 32px;
  border-radius: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-light);
}

.home__featuredTitle {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 32px;
  color: var(--text-primary);
  position: relative;
  padding-left: 0;
}

.home__featuredTitle::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 60px;
  height: 4px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

/* Responsive design */
@media (max-width: 1200px) {
  .home__row {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .home__image {
    height: 250px;
    margin-bottom: -100px;
  }
  
  .home__row:first-of-type {
    margin-top: 80px;
  }
  
  .home__featured {
    margin: 10px;
    padding: 15px;
  }
  
  .home__featuredTitle {
    font-size: 20px;
  }
}