.checkout {
  display: flex;
  padding: 32px 24px;
  background-color: var(--background-light);
  min-height: calc(100vh - 80px);
  gap: 32px;
  max-width: 1400px;
  margin: 0 auto;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 24px;
  border-radius: 16px;
  box-shadow: var(--shadow-sm);
}

.checkout__left {
  flex: 1;
  background-color: var(--background-white);
  padding: 32px;
  border-radius: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-light);
}

.checkout__right {
  width: 350px;
  min-width: 350px;
  height: fit-content;
}

.checkout__title {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 2px solid var(--border-light);
  font-size: 32px;
  font-weight: 700;
  color: var(--text-primary);
  position: relative;
}

.checkout__title::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 80px;
  height: 2px;
  background: var(--gradient-primary);
}

.checkout__greeting {
  font-size: 18px;
  margin-bottom: 16px;
  color: var(--text-secondary);
  font-weight: 500;
}

.checkout__empty {
  padding: 60px 20px;
  text-align: center;
}

.checkout__emptyTitle {
  font-size: 28px;
  margin-bottom: 16px;
  color: var(--text-primary);
  font-weight: 700;
}

.checkout__emptyInfo {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: 32px;
  line-height: 1.6;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.checkout__emptyButton {
  background: var(--gradient-primary);
  color: white;
  cursor: pointer;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  box-shadow: var(--shadow-sm);
  transition: all 0.2s ease;
}

.checkout__emptyButton:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Responsive design */
@media (max-width: 1024px) {
  .checkout {
    flex-direction: column;
  }
  
  .checkout__left {
    margin-right: 0;
    margin-bottom: 20px;
    min-width: auto;
  }
  
  .checkout__right {
    width: 100%;
  }
}