.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: auto;
  padding: 32px;
  background-color: var(--background-white);
  border-radius: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-light);
}

.subtotal__title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 24px;
  color: var(--text-primary);
}

.subtotal__price {
  font-size: 20px;
  margin-bottom: 16px;
  color: var(--text-primary);
}

.subtotal__price strong {
  color: var(--accent-purple);
  font-size: 28px;
  font-weight: 700;
}

.subtotal__items {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: 24px;
  font-weight: 500;
}

.subtotal__gift {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  padding: 16px;
  background: var(--background-light);
  border-radius: 8px;
}

.subtotal__gift > input {
  margin-right: 12px;
  cursor: pointer;
  width: 18px;
  height: 18px;
  accent-color: var(--accent-blue);
}

.subtotal__gift > label {
  font-size: 16px;
  color: var(--text-primary);
  cursor: pointer;
  font-weight: 500;
}

.subtotal > button {
  background: var(--gradient-primary);
  border-radius: 12px;
  width: 100%;
  height: 52px;
  border: none;
  margin-top: 16px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
}

.subtotal > button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.subtotal > button:active {
  transform: translateY(0);
}

.subtotal > button:disabled {
  background: var(--border-medium);
  color: var(--text-muted);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.subtotal__notice {
  font-size: 14px;
  color: var(--text-secondary);
  margin-top: 16px;
  line-height: 1.5;
}

.subtotal__eligibility {
  font-size: 14px;
  color: var(--success);
  margin-top: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 6px;
}

.subtotal__eligibility::before {
  content: '✓';
  font-weight: bold;
}