.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px;
  background: var(--background-white);
  border-radius: 16px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-light);
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.product:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-4px);
  border-color: var(--accent-blue);
}

.product::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.product:hover::before {
  transform: scaleX(1);
}

.product__info {
  flex: 1;
  margin-bottom: 20px;
  text-align: left;
}

.product__info > p {
  font-size: 16px;
  line-height: 1.5;
  color: var(--text-primary);
  font-weight: 500;
  margin-bottom: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  transition: color 0.2s ease;
}

.product:hover .product__info > p {
  color: var(--accent-blue);
}

.product__price {
  margin: 12px 0;
  display: flex;
  align-items: baseline;
  gap: 6px;
}

.product__price small {
  font-size: 14px;
  color: var(--text-muted);
  font-weight: 500;
}

.product__price strong {
  font-size: 28px;
  color: var(--accent-purple);
  font-weight: 700;
}

.product__rating {
  display: flex;
  align-items: center;
  margin: 12px 0;
  gap: 12px;
}

.product__stars {
  display: flex;
  color: var(--warning);
  font-size: 16px;
}

.product__ratingCount {
  color: var(--text-secondary);
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.product__ratingCount:hover {
  color: var(--accent-blue);
}

.product > img {
  max-height: 220px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
  border-radius: 8px;
}

.product:hover > img {
  transform: scale(1.02);
}

.product > button {
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
}

.product > button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.product > button:active {
  transform: translateY(0);
}

.product__badge {
  background: var(--gradient-secondary);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 700;
  margin-bottom: 12px;
  align-self: flex-start;
  box-shadow: var(--shadow-sm);
}

.product__shipping {
  color: var(--success);
  font-size: 14px;
  font-weight: 600;
  margin: 6px 0;
  display: flex;
  align-items: center;
  gap: 4px;
}

.product__shipping::before {
  content: '✓';
  font-weight: bold;
}

.product__prime {
  color: var(--accent-teal);
  font-size: 14px;
  font-weight: 600;
  margin: 6px 0;
  padding: 4px 8px;
  background: rgba(49, 151, 149, 0.1);
  border-radius: 6px;
  display: inline-block;
}

/* Responsive design */
@media (max-width: 768px) {
  .product {
    max-width: 300px;
    min-height: 400px;
    margin: 5px;
    padding: 15px;
  }
  
  .product > img {
    max-height: 150px;
  }
