wishlist {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 2;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.product__wishlist:hover {
  background: rgba(255, 255, 255, 1);
  transform: scale(1.1);
}

.product__wishlist-icon {
  font-size: 20px;
  color: var(--text-muted);
  transition: color 0.2s ease;
}

.product__wishlist-icon.active {
  color: var(--error);
}

.product__wishlist:hover .product__wishlist-icon {
  color: var(--error);
}

.product__image {
  cursor: pointer;
}

.product__title {
  cursor: pointer;
}

.product__title:hover {
  color: var(--accent-blue);
}.produ
ct__buttons {
  display: flex;
  gap: 10px;
  width: 100%;
}

.product__cart-button {
  flex: 2;
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-sm);
}

.product__cart-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.product__cart-button:active {
  transform: translateY(0);
}

.product__compare-button {
  flex: 1;
  background: transparent;
  color: var(--accent-blue);
  border: 1px solid var(--accent-blue);
  border-radius: 12px;
  padding: 14px 10px;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.2s ease;
  cursor: pointer;
}

.product__compare-button:hover {
  background: rgba(25, 118, 210, 0.1);
}